/* signup.css */

.Signup {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full viewport height */
}

/* Form Styling with Soft Shadow and Rounded Corners */
.Signup form {
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    padding: 30px; /* Increased padding for better spacing */
    max-width: 450px; /* Slightly wider form for potential labels */
    width: 100%;
}

/* Heading with Subtle Gradient and Optional Icon */
.Signup h1 {
    text-align: center;
    margin-bottom: 25px;
    background: linear-gradient(to right, #435978, #324159); /* Optional gradient */
    color: #fff; /* White text on gradient */
    padding: 10px 20px; /* Optional padding for icon */
    border-radius: 10px; /* Rounded corners for a polished look */

    /* Optional icon (replace with your desired icon class) */
    /* font-size: 1.5em;  */
    /* margin-right: 10px;  */
    /* content: "\f090";  */
    /* font-family: FontAwesome;  */ /* Example using Font Awesome */
}

/* Labels with Improved Readability */
.Signup label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold; /* Emphasize labels for clarity */
}

/* Input Fields with Refined Styling */
.Signup input {
    width: 100%;
    padding: 12px 18px; /* Consistent padding for better proportions */
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box; /* Ensure padding doesn't affect width */
}

/* Buttons with Consistent Styling and Hover Effects */
.Signup button {
    width: 100%;
    padding: 12px 18px; /* Consistent padding for buttons */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.Signup button:hover {
    background-color: slightlydarker(color(button)); /* Darken base color on hover */
}

/* Style the 'Sign Up' button distinctly */
.Signup button[type="submit"]:last-child {
    background-color: #4CAF50; /* Green for positive action */
}

.Signup button[type="submit"]:last-child:hover {
    background-color: #388E3C; /* Darker green on hover */
}